@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{background-image:url(data:image/png;base64,);background-size:100px;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;min-height:100vh}.drum-machine{display:block}#display{font-family:'VT323', monospace;font-size:30px;color:white;text-align:center;border:6px solid #d6d6d6;border-radius:10px;width:500px;min-height:60px;background-color:black;margin-bottom:20px}#drums{padding:20px 0px 20px 20px;width:500px;min-height:200px;background-color:black;color:white;font-family:'VT323', monospace;font-size:40px;display:grid;grid-template-columns:repeat(3, 30%);grid-template-rows:repeat(3, 30%);grid-gap:10px;text-align:center;border:6px solid #d6d6d6;border-radius:10px}#drums .drum-pad{border:1px solid rgba(255,255,255,0.2);height:60px;-webkit-align-self:center;align-self:center;background-color:black}#drums .drum-pad:hover{background-color:purple}

